.subscription-list.no-js {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 1fr 1fr 1fr; }
  @media (max-width: 1199px) {
    .subscription-list.no-js {
      grid-template-rows: 1fr;
      justify-items: center; } }

.no-js.trainers-list {
  max-width: unset;
  overflow: unset; }

.no-js.trainers-list__wrapper {
  display: flex;
  flex-wrap: wrap;
  gap: 30px;
  overflow: unset; }
  @media (max-width: 1199px) {
    .no-js.trainers-list__wrapper {
      justify-content: center; } }

.no-js.trainers__button {
  display: none; }

